		background: linear-gradient(to bottom,#d4e2ea 0%,rgba(194,209,218,.12) 52%,#ffffff 100%) !important; } }
.overflow {
	overflow: hidden; }

.d-block {
	display: block !important; }

.transition {
	transition: 0.3s; }

.none {
	display: none; }
